:root {
  --color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  text-wrap: nowrap;
  box-sizing: border-box;
  justify-content: space-between;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

a {
  align-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  color: #0000;
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  line-height: 14.06px;
  text-decoration: none;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--color);
}

.is-active:before {
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
